.banner-single {
    img {
        min-height: 150px;
        object-fit: cover;
    }

    .container-fluid {
        @media screen and (max-width: 767px){
            padding-left: 0;
            padding-right: 0;
        }
    }

    .cat-banner__caption {
        left: 0;
        transform: translateY(-50%);
        width: 100%;
        text-align: center;
    }
}

.menu-all-service {
    color: var(--dark);
    margin-bottom: 40px;

    .menuSwiper {
        position: relative;
        overflow: hidden;
        border-bottom: 1px solid #E9E9E9;

        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 126px;
            background-image: linear-gradient(to left, rgba(255, 0, 0, 0), #fff);
            z-index: 1;
            @media screen and (min-width: 768px){
                left: auto;
                right: 0;
                background-image: linear-gradient(to right, rgba(255, 0, 0, 0), #fff);
            }
        }
    }

    .main-menu {
        &__item {
            font-family: $main;
            font-weight: 600;
            font-size: 14px;
            line-height: 24px;
            letter-spacing: -0.005em;
            color: var(--l-gray);
            border-bottom: 1px solid transparent;

            &.is-active {
                border-color: var(--red);
            }

            a {
                display: block;
                padding: 15px 0;
            }

            img {
                margin-right: 10px;
            }
        }
    }
}

.service-package {
    margin-bottom: 40px;
    @media screen and (min-width: 768px) {
        margin-bottom: 32px;
    }

    &.dark, &.gray {
        padding: 40px 0;
        @media screen and (min-width: 768px) {
            padding: 32px 0;
        }
    }

    &.dark {
        background-color: var(--dark);
        .service__content {
            color: var(--l-gray2);
        }
    }

    &.gray {
        background-color: var(--l-gray2);
        margin-bottom: 0;
    }

    .service__content {
        font-size: 14px;
        line-height: 24px;
        letter-spacing: -0.5px;
        padding-left: 0;

        .btn--border-red{
            @media screen and (max-width: 640px){
                font-size: 14px;
                width: 100%;
            }
            &:after{
                @include transform(rotate(325deg));
                right: -6px;
                @media screen and (max-width: 640px){
                    transform: rotate(338deg);
                    right: -12px;
                }
            }
        }

        .title, p, ul {
            margin-bottom: 8px;
            @media screen and (min-width: 768px){
                margin-bottom: 24px;
            }
        }

        ul {
            li {
                margin-bottom: 16px;
                img {
                    margin-top: -4px;
                    margin-right: 10px;
                }
                &:last-child {
                    margin-bottom: 0;
                }
                a {
                    color: inherit;
                }
            }
        }

        .btn-service-red{
            background: var(--red);
            color: var(--white);
            @media screen and (max-width: 640px){
                font-size: 14px;
                width: 100%;
            }
            &:hover{
                color: var(--dark);
            }
        }
    }
}